<template>
  <div class="topic">
      <van-nav-bar
        class="backBar"
        :title="topicTitle"
        left-arrow
        @click-left="onClickLeft"
      />

      <van-list
        class="vod-list"
        finished-text="没有更多了"
      >
          <van-row type="flex" 
            v-for="(outList,outIndex) of vodsgrid"
            :key="outIndex">
              <van-col
                v-for="vod of outList" 
                :key="vod.index"
                class="vod-class"
                @click="vodClick(vod.value.id)"
                >
                  <div class="vod-container" :style="vodStyle">
                    <img v-lazy="vod.value.imgy" :style="vodStyle"/>
                    <span class="span-mark">{{ vod.value.remarks }}</span>
                  </div>
                  <span class="vod-name" :style="vodNameStyle">
                    {{ vod.value.name }}
                  </span>
              </van-col>
          </van-row>
      </van-list>
   </div>
</template>

<script>
import { NavBar,Row, Col,List } from 'vant'
import 'vant/lib/nav-bar/style'
import 'vant/lib/row/style'
import 'vant/lib/col/style'
import 'vant/lib/list/style'

function chunkArrayWithIndex(array, chunkSize) {
  let result = [];
  for (let i = 0; i < array.length; i += chunkSize) {
    // 使用map来创建一个新数组，其中包含原始索引和值
    const chunkWithIndex = array.slice(i, i + chunkSize).map((item, index) => {
      return { index: i + index, value: item };
    });
    // 将新创建的带索引的数组添加到结果数组中
    result.push(chunkWithIndex)
  }
  return result;
}

export default {
   name:'Topicpage',
   components: {
     [NavBar.name]:NavBar,
     [Row.name]:Row,
     [Col.name]:Col,
     [List.name]:List,
   },
   data () {
    return {
       topicTitle:'',
       vods: [],
       vodsgrid:[],
    }
   },
   activated() {
     this.vods = []
     this.vodsgrid = []
     this.topicTitle = ''
     this.topicDetail()
   },
   watch : {
     vods () {
       this.vodsgrid = chunkArrayWithIndex(this.vods,3)
     }
   },
   computed : {
    vodStyle() {
      let widthtep = 2.34 
      let heighttep = 3.11 
      if (this.$utils.vodlistWidth > 0) {
        widthtep = this.$utils.vodlistWidth
        heighttep = this.$utils.vodlistHeight
      }
      return {
        width: widthtep + 'rem',
        height: heighttep + 'rem',
        marginRight: 0.2 + 'rem',
        marginBottom: 0.2 + 'rem',
      }
     },
     vodNameStyle () {
        let widthtep = 2.34 
        if (this.$utils.vodlistWidth > 0) {
          widthtep = this.$utils.vodlistWidth
        }
        return {
          width: widthtep + 'rem',
          height: '0.32rem',
          position: 'absolute',
          whiteSpace: 'nowrap',
          overflow: 'hidden',
          textOverflow: 'ellipsis',
          // marginTop: '0.15rem',
          fontSize: '0.26rem',
          color: '#444444',
          // marginBottom: '0.33rem',
          textAlign: 'center'
        }
     }
   },
   methods: {
    onClickLeft() {
      this.$router.back(-1)
    },
    topicDetail() {
      this.$http.get('/api/jjsp',{
            params: {
             id: this.$route.params.id
            }
      }).then((res)=>{
        console.log(res)
         const result = res.data
         if(result.code === 1000 && result.data){
           const topic = result.data
           this.topicTitle = topic.title
           this.vods.push(...topic.vod)
         }
      })
    },
    vodClick (id) {
      this.$router.push({ path: `/detail/${id}` })
    }
  }
}
</script>

