  margin-left: .2rem
  margin-top: .95rem
  .vod-class
    margin-bottom: .43rem
    position relative
  .vod-container
    position relative
    .span-mark
      position: absolute
      width 90%
      right 0
      height: .22rem
      bottom: 0
      font-size: .21rem
      text-align: right
      color: #fff
      padding: .01rem .16rem .05rem 0 //上右下左
      background-image: linear-gradient(to bottom, rgba(0,0,0,0), rgba(0,0,0,0.7))   
</style>